@charset "utf-8";

.project-card-img {
 display: block;
 margin: 0 auto; // Center the image
 width: 100% !important; // Override Bootstrap's 80%
 max-height: 260px;
 object-fit: contain;
 padding: 10px;
 border: 0px solid #ddd;
 border-radius: 10px;
 background-color: #fff;
}

// Dimensions
$max-content-width: 930px;

@import
 "variables",
 "themes",
 "layout",
 "base",
 "distill",
 "cv",
 "tabs",
 "typograms",
 "font-awesome/fontawesome",
 "font-awesome/brands",
 "font-awesome/solid",
 "font-awesome/regular",
 "tabler-icons/tabler-icons.scss",
 "tabler-icons/tabler-icons-filled.scss",
 "tabler-icons/tabler-icons-outline.scss"
